<template>
    <span style="word-break: break-word;">
        <slot></slot>
    </span>
</template>

<script>
export default {
    props: {
        targetLineCount: {
            default: 1,
            type: Number
        },
        unit: {
            default: "em",
            type: String
        },
        min: {
            default: 0.5,
            type: Number
        },
        max: {
            default: 3,
            type: Number
        }
    },
    data() {
        return {
            observer: null
        };
    },
    methods: {
        calculate() {
            const element = this.$el;
            // first make it an inline block and set the line height to a fixed pixel value
            element.style.display = "inline-block";
            element.style.lineHeight = "1px";
            // then keep trying untill it fits
            let fontSize = this.max;
            const stepSize = this.unit === "px" ? 1 : 0.05;
            element.style.fontSize = fontSize + this.unit;
            while (element.offsetHeight > this.targetLineCount && fontSize > this.min) {
                console.log(this.$el.innerHTML, fontSize);
                fontSize -= stepSize;
                element.style.fontSize = fontSize + this.unit;
            }

            if (this.targetLineCount < 4 && fontSize < 1.5) {
                this.targetLineCount += 1;
                this.calculate();
                return;
            }
            // found it!!
            // reset the styles
            element.style.display = null;
            element.style.lineHeight = null;
        }
    },
    mounted() {
        this.calculate();
        if ("MutationObserver" in window && this.observer === null) {
            // Create the observer (and what to do on changes...)
            this.observer = new MutationObserver(this.calculate);
            // Setup the observer
            this.observer.observe(this.$el, { subtree: true, characterData: true });
        }
    },
    beforeDestroy: function() {
        // Clean up
        this.observer.disconnect();
    }
};
</script>
